                // hearder
                header {
                    background-image: linear-gradient(to right bottom, rgba($color-primary-light, .8), rgba($color-primary-dark, 0.8)), url(../images/header.png);
                    background-repeat: no-repeat;
                    background-size: cover;
                    height: 100vh;
                    clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
                    position: relative;
                    z-index: 1;
                    // navbar
                    .navbar {
                        padding: 30px 0px;
                        // nav
                        .slide-bar {
                            position: fixed;
                            @extend %position;
                            height: 100vh;
                            background: rgba($color-black, .8);
                            visibility: hidden;
                            opacity: 0;
                            z-index: 2;
                            @extend %trans;
                            &.open {
                                visibility: visible;
                                opacity: 1;
                            }
                            .search {
                                position: fixed;
                                justify-content: space-between;
                                width: 300px;
                                height: 80px;
                                top: 0px;
                                padding: 0px 15px;
                                font-size: 30px;
                                z-index: 3;
                                cursor: pointer;
                                @extend %flex;
                                @extend %trans;
                            }
                            .form {
                                @extend %flex;
                                justify-content: center;
                                position: fixed;
                                opacity: 0;
                                @extend %position;
                                z-index: 8;
                                background: rgba($color-black-dark, .9);
                                visibility: hidden;
                                z-index: 4;
                                &.open {
                                    opacity: 1;
                                    visibility: visible;
                                }
                            }
                            form {
                                width: 400px;
                                input {
                                    border-bottom: 1px solid $white-color;
                                    width: 400px;
                                    color: $color-primary-white;
                                }
                                .open-search-form {
                                    font-size: 50px;
                                    color: $color-primary-white;
                                    text-align: center;
                                    font-weight: 300;
                                    cursor: pointer;
                                }
                            }
                        }
                        nav {
                            position: absolute;
                            top: 0px;
                            left: -100%;
                            width: 300px;
                            height: 100%;
                            background-color: $color-primary-white;
                            @extend %trans;
                            &.left {
                                left: 0;
                            }
                            ul {
                                margin-top: 100px;
                                text-align: center;
                                font-size: 30px;
                                display: block;
                                a {
                                    color: $color-greey-dark;
                                    font-size: 16px;
                                    font-family: 'Montserrat-Bold';
                                }
                            }
                        }
                        // toggle
                        .toggle {
                            position: fixed;
                            width: 50px;
                            height: 50px;
                            top: 30px;
                            right: 35px;
                            cursor: pointer;
                            background-color: $white-color;
                            border-radius: 50%;
                            i {
                                @extend %flex;
                                justify-content: center;
                                line-height: 50px;
                                font-size: 22px;
                            }
                        }
                    }
                    // header-content
                    .header-logo-box {
                        @include Sm {
                            width: 100px;
                            height: 100px;
                            img {
                                width: 100%;
                                height: auto;
                            }
                        }
                    }
                    .header-text-box {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        text-align: center;
                        z-index: 1;
                        @include Sm {
                            width: 80%;
                        }
                        @include mob480 {
                            width: 90%;
                        }
                        .header-primary {
                            color: $white-color;
                            text-transform: uppercase;
                            backface-visibility: hidden;
                            margin-bottom: 50px;
                            .header-primary-main {
                                display: block;
                                font-size: 60px;
                                font-weight: 400;
                                letter-spacing: 30px;
                                animation: left 1s ease-in-out;
                                line-height: 1.7;
                                @include Lg {
                                    font-size: 40px;
                                }
                                @include Sm {
                                    font-size: 60px;
                                }
                                @include Xs {
                                    font-size: 40px;
                                    letter-spacing: 15px;
                                }
                                @include mob425 {
                                    font-size: 25px;
                                    letter-spacing: 12px;
                                }
                            }
                            .header-primary-sub {
                                display: block;
                                font-size: 20px;
                                letter-spacing: 17px;
                                font-weight: 600;
                                animation: right 1s ease-in-out;
                                @include Lg {
                                    font-size: 17px;
                                    letter-spacing: 14px;
                                }
                                @include Sm {
                                    font-size: 15px;
                                    letter-spacing: 20px;
                                }
                                @include Xs {
                                    font-size: 14px;
                                    letter-spacing: 10px;
                                }
                                @include mob425 {
                                    letter-spacing: 5px;
                                }
                            }
                        }
                    }
                }
                
                // header
                // about-us
                .about-us {
                    margin-top: -20vh;
                    padding: 150px 0px;
                    text-align: left;
                    @include Sm {
                        padding-bottom: 70px;
                    }
                    @include mob480 {
                        text-align: center;
                    }
                    h2 {
                        background-clip: text;
                        color: transparent;
                        font-weight: 700;
                        margin-bottom: 80px;
                        @extend %trans;
                        @include Lg {
                            margin-bottom: 40px;
                        }
                        @include Xs {
                            font-size: 25px;
                        }
                        &:hover {
                            transform: skewY(2deg) skewX(15deg);
                        }
                    }
                    .row {
                        @include Sm {
                            flex-direction: column-reverse;
                        }
                        @include Sm {
                            .content {
                                max-width: 100%;
                            }
                        }
                    }
                    .images {
                        @include Sm {
                            margin: auto;
                            height: 300px;
                            max-width: 300px;
                        }
                    }
                    .title {
                        color: $color-black-dark;
                        font-size: 25px;
                        margin-top: 30px;
                        text-transform: capitalize;
                        font-family: 'Montserrat-Bold';
                        @include Lg {
                            font-size: 20px;
                        }
                    }
                    .paragraph {
                        font-size: 16px;
                        color: $color-greey-dark;
                        font-weight: 500;
                        font-family: 'Montserrat-Light';
                        line-height: 1.7;
                        @include Lg {
                            font-size: 14px;
                        }
                    }
                    .link {
                        font-size: 16px;
                        border-bottom: 1px solid $color-primary;
                        color: $color-primary;
                        padding: 3px;
                        font-family: 'Montserrat-Bold';
                        i {
                            margin-left: 10px;
                            font-size: 14px;
                        }
                        &:hover {
                            color: $color-primary-white;
                            background-color: $color-primary;
                            box-shadow: 1px 5px 10px $color-black;
                            @extend %trans;
                            padding: 10px 15px;
                            transform: translateY(2px);
                        }
                        &:active {
                            transform: translateY(0);
                        }
                    }
                    .img {
                        overflow: hidden;
                        width: 100%;
                        height: 100%;
                        &-photo {
                            width: 100%;
                            height: 100%;
                            border-radius: 3px;
                            box-shadow: 0px 15px 20px rgba($color-black, .4);
                            z-index: 10;
                            outline-offset: 10px;
                            @extend %trans;
                            &:hover {
                                transform: scale(1.05);
                                z-index: 20;
                            }
                        }
                        &:hover .img-photo:not(:hover) {
                            transform: scale(.8);
                        }
                    }
                }
                
                // feature-box
                .feature-box {
                    background-image: linear-gradient(to right bottom, rgba($color-primary-light, .8), rgba($color-primary-dark, 0.8)), url(../images/4.png);
                    background-repeat: no-repeat;
                    background-size: cover;
                    padding: 200px 0px 150px;
                    transform: skewY(-7deg);
                    @include Lg {
                        padding: 100px 0px;
                    }
                    &>* {
                        transform: skewY(7deg);
                    }
                    .content {
                        @include Sm {
                            justify-content: space-between;
                            flex-flow: wrap;
                        }
                        @extend %flex;
                    }
                    .card {
                        margin: 0px 1%;
                        text-align: center;
                        box-shadow: 0px 25px 40px rgba($color-black, .5);
                        @extend %trans;
                        cursor: pointer;
                        @include Sm {
                            padding: 20px;
                            margin-bottom: 20px;
                        }
                        &:hover {
                            transform: translateY(10px)scale(1.05);
                        }
                        .card-title {
                            color: $color-primary;
                            font-size: 60px;
                            margin: 0;
                        }
                        .card-body {
                            @include Lg {
                                padding: 10px;
                            }
                            h3 {
                                color: $color-black-dark;
                                font-size: 18px;
                            }
                            p {
                                color: $color-greey-dark;
                                font-size: 14px;
                                font-family: 'Montserrat-Light';
                                @include Lg {
                                    font-size: 13px;
                                }
                            }
                        }
                    }
                }
                
                // // feature-box
                // testmonial
                .testmonial {
                    padding: 70px 0px;
                    text-align: center;
                    background-color: $color-blue;
                    margin-top: -5rem;
                    h2 {
                        background-clip: text;
                        color: transparent;
                        font-weight: 700;
                        margin: 5rem 0rem;
                        @include Sm {
                            margin-bottom: 0;
                        }
                    }
                    .content {
                        @include Sm {
                            max-width: 50%;
                            flex: 0 0 50%;
                            margin: 30px 0px;
                        }
                        @include Xs {
                            max-width: 70%;
                            flex: 0 0 70%;
                            margin: 30px auto;
                        }
                        @include mob480 {
                            max-width: 80%;
                            flex: 0 0 80%;
                        }
                        @include mob425 {
                            max-width: 100%;
                            flex: 0 0 100%;
                        }
                    }
                    & .card {
                        perspective: 150rem;
                        position: relative;
                        background-color: $color-blue;
                        border: none;
                        height: 30rem;
                        @include Sm {}
                        & .card-slide {
                            color: $color-black;
                            @extend %position;
                            height: 30rem;
                            backface-visibility: hidden;
                            border-radius: 3px;
                            box-shadow: 0px 25px 40px rgba($color-black, .5);
                            @extend %trans;
                            @include Lg {
                                height: 32rem;
                            }
                        }
                        // card front style
                        .card-photo {
                            background-size: cover;
                            height: 15rem;
                            background-blend-mode: screen;
                            clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
                        }
                        // card-head
                        & .card-head {
                            .title {
                                font-size: 30px;
                                position: absolute;
                                top: 120px;
                                right: 30px;
                                width: 70%;
                                text-align: right;
                                color: $color-primary-white;
                                font-weight: 300;
                                @include Lg {
                                    font-size: 25px;
                                    top: 150px;
                                }
                                .card-title-span {
                                    padding: 10px 15px;
                                    line-height: 1.7;
                                    box-decoration-break: clone;
                                    text-transform: uppercase;
                                    &.card-title-span-1 {
                                        background-image: linear-gradient(to right bottom, $color-second-light, $color-second-white);
                                    }
                                    &.card-title-span-2 {
                                        background-image: linear-gradient(to right bottom, $color-primary-dark, $color-primary-light);
                                    }
                                    &.card-title-span-3 {
                                        background-image: linear-gradient(to right bottom, $color-thirty-light, $color-thirty-white);
                                    }
                                }
                            }
                        }
                        // .card-head
                        & .card-bodys {
                            ul {
                                width: 70%;
                                margin: auto;
                                @include Lg {
                                    margin: 20px auto;
                                }
                                li {
                                    font-size: 15px;
                                    border-bottom: 1px solid $white-color;
                                    padding: 10px;
                                    color: $color-greey-dark;
                                    text-align: center;
                                    font-weight: 500;
                                    font-family: 'Montserrat-Bold';
                                    @include Lg {
                                        font-size: 12px;
                                        padding: 12px;
                                    }
                                }
                            }
                        }
                        // card front
                        // card back
                        & .card-slide {
                            .card-details {
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                                text-align: center;
                                p {
                                    font-size: 25px;
                                    text-transform: uppercase;
                                    color: $color-primary-white;
                                    font-family: 'Montserrat-Light';
                                }
                                h3 {
                                    font-size: 60px;
                                    color: $color-primary-white;
                                    font-weight: 300;
                                }
                                .link-scroll {
                                    margin-top: 40px;
                                    width: 150px;
                                    height: 50px;
                                    line-height: 50px;
                                    padding: 0;
                                }
                            }
                        }
                        & .card-slide-back-1 {
                            background-image: linear-gradient(to right bottom, $color-second-light, $color-second-white);
                        }
                        & .card-slide-back-2 {
                            background-image: linear-gradient(to right bottom, $color-primary-dark, $color-primary-light);
                        }
                        & .card-slide-back-3 {
                            background-image: linear-gradient(to right bottom, $color-thirty-light, $color-thirty-white);
                        }
                        & .card-slide-back {
                            transform: rotateY(-180deg);
                        }
                        &:hover .card-slide-front {
                            transform: rotateY(180deg);
                        }
                        &:hover .card-slide-back {
                            transform: rotateY(0deg);
                        }
                    }
                    // popup
                    .popup {
                        position: fixed;
                        width: 100%;
                        background: $color-black-dark;
                        height: 100vh;
                        z-index: 999999;
                        top: 0;
                        left: 0;
                        opacity: 0;
                        visibility: hidden;
                        overflow: hidden;
                        @extend %trans;
                        .popup-inner {
                            width: 90%;
                            margin: auto;
                            @extend %flex;
                            justify-content: space-between;
                            position: absolute;
                            transform: translate(-50%, -50%);
                            top: 50%;
                            left: 50%;
                            background-color: $color-primary-white;
                            overflow: hidden;
                            @include Xs {
                                display: block;
                                width: 100%;
                            }
                            .close {
                                position: absolute;
                                right: 0;
                                top: 0;
                                width: 30px;
                                height: 30px;
                                cursor: pointer;
                                z-index: 9999;
                                background-color: #c30b2a;
                                color: $white-color;
                                font-weight: 200;
                                font-size: 30px;
                            }
                            .popup-photo {
                                width: 30%;
                                height: 100%;
                                position: relative;
                                @include Xs {
                                    width: 100%;
                                    display: flex;
                                }
                                .card-head {
                                    shape-outside: circle();
                                    overflow: hidden;
                                    position: relative;
                                    width: 100%;
                                    height: 50%;
                                    .photo {
                                        position: relative;
                                        width: 100%;
                                        height: 100%;
                                        &::after {
                                            position: absolute;
                                            content: 'MARY SMITH';
                                            left: 50%;
                                            top: 50%;
                                            transform: translate(-50%, -50%);
                                            opacity: 0;
                                            color: $white-color;
                                            @extend %trans;
                                        }
                                    }
                                    img {
                                        width: 100%;
                                        height: 200px;
                                        object-fit: cover;
                                        @extend %trans;
                                    }
                                    &:hover img {
                                        transform: scale(1.5);
                                        cursor: pointer;
                                        background-image: -webkit-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                                        background-image: -ms-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                                        opacity: 0.549;
                                    }
                                    &:hover .photo::after {
                                        opacity: 1;
                                    }
                                }
                            }
                            .popup-body {
                                width: 70%;
                                padding: 0px 30px;
                                text-align: left;
                                color: $color-primary-white;
                                @include Xs {
                                    width: 100%;
                                    padding-bottom: 30px;
                                }
                                h2 {
                                    margin: 20px 0px;
                                    color: $color-primary;
                                    @include Sm {
                                        margin: 0px;
                                    }
                                    @include Xs {
                                        font-size: 25px;
                                        margin: 20px 0px;
                                    }
                                }
                                h3 {
                                    font-size: 22px;
                                    color: $color-black-dark;
                                    text-transform: capitalize;
                                    margin-bottom: 20px;
                                    font-family: 'Montserrat-Regular';
                                    @include Lg {
                                        font-size: 20px;
                                    }
                                    @include Sm {
                                        margin: 20px 0px 10px;
                                    }
                                    @include Xs {
                                        font-size: 18px;
                                    }
                                }
                                .pargraph {
                                    display: flex;
                                    justify-content: space-between;
                                    color: $color-greey-dark;
                                    text-align: justify;
                                    column-gap: 50px;
                                    font-family: 'Montserrat-Light';
                                    @include Sm {
                                        display: block;
                                    }
                                    p {
                                        font-size: 16px;
                                        line-height: 1.7;
                                        @include Lg {
                                            font-size: 14px;
                                        }
                                        @include Sm {
                                            margin-bottom: 10px;
                                        }
                                        @include Xs {
                                            font-size: 12px;
                                        }
                                    }
                                }
                                .close-scroll {
                                    background: $color-primary-dark;
                                    font-size: 14px;
                                    color: $white-color;
                                    margin-top: 20px;
                                    padding: 10px 30px;
                                    font-family: 'Montserrat-Regular';
                                    @include Sm {
                                        margin: 10px;
                                    }
                                }
                            }
                        }
                    }
                    .popup:target {
                        visibility: visible;
                        opacity: 1;
                    }
                    a.link-btn.btn-last {
                        background: #00c269;
                        color: $white-color;
                        margin-top: 60px;
                        @include Lg {
                            margin-top: 100px;
                        }
                        @include Sm {
                            margin-top: 60px;
                        }
                    }
                }
                
                // 
                // people-say
                .say-people {
                    padding: 60px 0px;
                    h2 {
                        margin: 0 0 20px;
                    }
                    .overlay {
                        margin-top: 50px;
                    }
                    .carousel-indicators {
                        margin-bottom: -50px;
                        li {
                            width: 10px;
                            height: 10px;
                            border-radius: 50%;
                            background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
                        }
                    }
                    .carousel {
                        @extend %flex;
                        @include Sm {
                            flex-direction: column;
                        }
                        .card {
                            width: 50%;
                            height: 100%;
                            flex-direction: row;
                            @include Sm {
                                width: 100%;
                            }
                        }
                    }
                    .card-head {
                        width: 80px;
                        height: 80px;
                        border-radius: 50%;
                        float: left;
                        shape-outside: circle();
                        overflow: hidden;
                        position: relative;
                        .photo {
                            position: absolute;
                            width: 100%;
                            height: 100%;
                            @extend %trans;
                            &::after {
                                position: absolute;
                                content: 'MARY SMITH';
                                font-size: 12px;
                                left: 50%;
                                top: 50%;
                                transform: translate(-50%, -50%);
                                opacity: 0;
                                color: $white-color;
                                @extend %trans;
                            }
                        }
                        img {
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                            object-fit: cover;
                            @extend %trans;
                        }
                        &:hover img {
                            transform: scale(1.5);
                            cursor: pointer;
                            background-image: -webkit-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                            background-image: -ms-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                            opacity: 0.549;
                        }
                        &:hover .photo::after {
                            opacity: 1;
                        }
                    }
                    .card-bodys {
