                        margin-left: 10px;
                        position: relative;
                        z-index: 1;
                        margin-top: 8px;
                        h3 {
                            font-size: 20px;
                            text-transform: capitalize;
                            font-family: 'Roboto-Regular';
                            margin-bottom: 10px;
                            color: $color-black;
                            @include Lg {
                                font-size: 18px;
                            }
                        }
                        p {
                            font-size: 14px;
                            font-family: 'Montserrat-Light';
                            margin-bottom: 20px;
                            color: $color-greey-dark;
                            line-height: 2;
                        }
                    }
                }
                
                // contact
                // contact
                .contact-us {
                    background-image: linear-gradient(to right bottom, #46ce59, #00b176);
                    padding: 100px 0px;
                    @include Sm {
                        padding: 50px 0px;
                    }
                    .contact {
                        background-image: url('../images/15.png');
                        background-size: cover;
                        height: 450px;
                        width: 70%;
                        margin: auto;
                        text-align: left;
                        @include Xs {
                            width: 90%;
                        }
                        .form {
                            position: relative;
                            width: 60%;
                            height: 100%;
                            overflow: hidden;
                            z-index: 1;
                            @include Sm {
                                width: 100%;
                            }
                            &::before {
                                content: '';
                                position: absolute;
                                left: -80px;
                                top: 0;
                                width: 100%;
                                height: 100%;
                                background: $white-color;
                                opacity: .9;
                                transform: skewX(-20deg);
                                transform-origin: left;
                                z-index: -1;
                                @include mob480 {
                                    left: 0;
                                    transform: skewX(0deg);
                                }
                            }
                            h2 {
                                margin: 20px 40px 50px;
                            }
                            input[type='text'],
                            input[type='email'] {
                                font-family: 'Montserrat-Light';
                                width: 60%;
                                margin: 20px 25px;
                                display: block;
                                height: 40px;
                                background-color: $white-color;
                                color: #999;
                                border: none;
                                padding: 20px;
                                @include mob480 {
                                    width: 80%;
                                }
                                &::placeholder {
                                    color: #ccc;
                                    font-weight: 400;
                                }
                            }
                            input[type=radio] {
                                display: none;
                            }
                            .radio {
                                margin: 30px 0 0 50px;
                                position: relative;
                                font-size: 18px;
                                color: #ccc;
                                font-weight: 400;
                                line-height: 1.5;
                                cursor: pointer;
                                @include Lg {
                                    display: block;
                                }
                            }
                            & span {
                                position: absolute;
                                left: -30px;
                                top: 5px;
                                width: 20px;
                                height: 20px;
                                border-radius: 50%;
                                border: 3px solid $color-primary-dark;
                                &::after {
                                    content: '';
                                    position: absolute;
                                    left: 50%;
                                    top: 50%;
                                    width: 7px;
                                    height: 7px;
                                    border-radius: 50%;
                                    transform: translate(-50%, -50%);
                                    opacity: 0;
                                    background-color: $color-primary-dark;
                                    transition: all .1s ease-in-out;
                                    display: block;
                                }
                            }
                        }
                        & input[type=radio]:checked~span::after {
                            opacity: 1;
                        }
                        button {
                            display: block;
                            margin: 40px;
                        }
                    }
                }
                
                // contact
                // footer
                footer {
                    background-color: $color-black-dark;
                    padding: 60px 0px;
                    text-align: left;
                    @include Xs {
                        padding-bottom: 20px;
                    }
                    .col-md-12 {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 80px;
                    }
                    .col-md-5 {
                        margin-left: 4%;
                        margin-right: 4%;
                        padding: 0;
                        @include Xs {
                            margin-bottom: 20px;
                        }
                        &::before {
                            content: '';
                            position: absolute;
                            top: -15px;
                            left: 0;
                            width: 100%;
                            height: 1px;
                            background-color: $color-greey-dark;
                            @include Xs {
                                content: none;
                            }
                        }
                        p {
                            font-size: 16px;
                            color: $color-primary-white;
                            font-family: 'Montserrat-Light';
                            padding-top: 10px;
                            @include Lg {
                                font-size: 14px;
                            }
                        }
                    }
                    .links {
                        text-transform: uppercase;
                        .plus {
                            @extend %flex;
                            justify-content: space-between;
                            color: $white-color;
                            cursor: pointer;
                            display: none;
                            @include Xs {
                                display: flex;
                            }
                            #plus-one {
                                position: relative;
                                &::after {
                                    position: absolute;
                                    right: 0;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    content: '\f067';
                                    font-family: "Font Awesome 5 free";
                                    font-weight: 700;
                                }
                                &.active:after {
                                    content: '\f068'!important;
                                }
                            }
                            a {
                                color: $white-color;
                                font-family: 'Montserrat-Bold';
                            }
                        }
                        ul {
                            padding: 0;
                            display: block;
                            @include Xs {
                                display: none;
                                &.active {
                                    display: block;
                                }
                            }
                            a {
                                color: $color-primary-white;
                                @extend %trans;
                                padding-top: 10px;
                                font-size: 16px;
                                @include Xs {
                                    font-size: 14px;
                                    color: $color-greey-dark;
                                }
                                &:hover {
                                    color: $color-primary;
                                }
                                @include Lg {
                                    display: block;
                                }
                            }
                        }
                    }
                }
                
                header.add {
                    clip-path: none;
                    & .navbar .toggle {
                        background-color: $main-color;
                        color: $white-color;
                    }
                }