// colors
$color-primary-light:#7ed56f;
$color-primary-dark:#28b485;
$color-primary-white:#fff;
$color-greey-dark:#7d7d7d;
$color-black:#000;
$color-black-dark:#333;
$color-blue:#eee;
$color-second-light:#ffb900;
$color-second-white:#ff7730;
$color-thirty-light:#2998ff;
$color-thirty-white:#5643fa;
// colors
$main-color: #00c269;
$color-primary:#00c269;
$white-color:#fff;
// animate
%trans {
    transition: all .7s ease-in-out;
}

%flex {
    display: flex;
    align-items: center;
}

%position {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

// BUTTON
button,
a:link,
button:active,
button:focus,
input,
input:focus {
    border: none;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    background-color: transparent;
}

.link-btn:link,
.link-btn:visited,
.btn {
    color: $color-primary-white;
    padding: 15px 40px;
    background-color: $color-primary-white;
    color: $color-greey-dark;
    border-radius: 100px;
    display: inline-block;
    position: relative;
    animation: bottom 1s ease-out .58s;
    animation-fill-mode: backwards;
    @extend %trans;
    text-transform: uppercase;
    font-family: 'Montserrat-Regular';
    &:hover {
        transform: translateY(-5px);
        box-shadow: 0px 10px 10px rgba($color-black, .2);
    }
    &:active {
        transform: translateY(-1px);
        box-shadow: 0px 10px 10px rgba($color-black, .2);
    }
    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        background-color: $color-primary-white;
        border-radius: 100px;
        display: inline-block;
        @extend %trans;
        @extend %position;
    }
    &:hover::after {
        transform: scaleX(1.4) scaleY(1.6);
        opacity: 0;
    }
}

.card {
    border: none;
}

// header
h2 {
    background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
    text-transform: capitalize;
    font-size: 30px;
    background-clip: text;
    color: transparent;
    font-weight: 700;
}

// button
// key frames
@keyframes left {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }
    80% {
        transform: translateX(10px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes right {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }
    80% {
        transform: translateX(-10px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes bottom {
    0% {
        transform: translateY(30px);
        opacity: 1;
    }
    80% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

// img
.card-photo-1 {
    background-image: linear-gradient(to right bottom, rgba($color-second-light, .5), rgba($color-primary-dark, 0.8)), url(../images/5.png);
}

.card-photo-2 {
    background-image: linear-gradient(to right bottom, rgba($color-second-light, .5), rgba($color-primary-dark, 0.8)), url(../images/6.png);
}

.card-photo-3 {
    background-image: linear-gradient(to right bottom, rgba($color-thirty-light, .5), rgba($color-primary-dark, 0.8)), url(../images/7.png);
}